function MyFloatingButtons(props) {
  const buttonclasses = ButtonStyle();
  return (
    <div>
      <FloatingButtons
        leftButtons={
          <div>
            <Button
              className={buttonclasses.leftButton}
              style={{ marginRight: '10px' }}
              variant="contained"
              color="secondary"
              size="small"
              onClick={props.saveTmp}
            >
              <div style={{ display: 'flex', flexDirection: 'column' }}>
                <Typography className={buttonclasses.buttonTitle}>
                  Save For Now
                </Typography>
              </div>
            </Button>
            <Button
              className={buttonclasses.root}
              variant="contained"
              color="primary"
              size="small"
              onClick={props.generateExeptionReport}
            >
              Generate Exception Report
            </Button>
          </div>
        }
        rightButtons={null}
      />
    </div>
  );
}

MyFloatingButtons.propTypes = {
  generateExeptionReport: PropTypes.func,
  saveTmp: PropTypes.func,
};

