  handleClickRow = (row: number[]) => {
    console.log('Row: ', row[0]);
    this.setFieldValue('partialRow', row[0]);
    this.openPartialModal();
  };

  handleClickRow2 = () => {};

  handleFilterChange = (filter: any) => {
    console.log('handleFilterChange', filter);
    let data = Object.assign({}, this.state.alltable);
    let all = Object.assign({}, this.state.alltableCopy);
    console.log('data from filter before: all', all.rows);
    console.log('data from filter before: data', data.rows);
    let filtered: any[] = [];
    let filter_name: string = '';
    let filter_issue: string = '';

    const defaultFilter = [
      {
        columnName: 'member_name',
        value: '',
      },
      {
        columnName: 'member_issue',
        value: '',
      },
    ];

    filter.forEach(item => {
      if (item.columnName === 'member_name')
        filter_name = item.value.toLowerCase();
      if (item.columnName === 'member_issue')
        filter_issue = item.value.toLowerCase();
    });

    if (filter.length > 0) {
      all.rows.forEach(item => {
        if (
          item.member_name.toLowerCase().includes(filter_name) &&
          item.member_issue.toLowerCase().includes(filter_issue)
        )
          filtered.push(item);
      });
      data.rows = filtered;
      console.log('data from filtered: ', filtered);
      console.log('data from filter: ', data.rows);
      console.log('data from filter all: ', all.rows);
      // if(filter_name === "" && filter_issue === ""){
      //     this.setState({alltable:all});
      // } else {

      // }

      this.setFieldValue('alltable', data);
      this.setFieldValue('defaultFilter', filter);
    } else {
      this.setFieldValue('alltable', all);
      this.setFieldValue('defaultFilter', defaultFilter);
    }
  };

  handleSortingChange = (sorting: any) => {
    console.log('handleSortingChange', sorting);
    let data = Object.assign({}, this.state.alltable);
    let all = Object.assign({}, this.state.alltableCopy);
    let sort_name: string = '';
    let sort_issue: string = '';

    sorting.forEach(item => {
      if (item.columnName === 'member_name') sort_name = item.direction;
      if (item.columnName === 'member_issue') sort_issue = item.direction;
    });

    if (sort_name === 'asc')
      all.rows.sort((a, b) => (a.member_name > b.member_name ? 1 : -1));
    if (sort_name === 'desc')
      all.rows.sort((a, b) => (a.member_name > b.member_name ? -1 : 1));
    if (sort_issue === 'asc')
      all.rows.sort((a, b) => (a.member_issue > b.member_issue ? 1 : -1));
    if (sort_issue === 'desc')
      all.rows.sort((a, b) => (a.member_issue > b.member_issue ? -1 : 1));

    if (sort_name === 'asc')
      data.rows.sort((a, b) => (a.member_name > b.member_name ? 1 : -1));
    if (sort_name === 'desc')
      data.rows.sort((a, b) => (a.member_name > b.member_name ? -1 : 1));
    if (sort_issue === 'asc')
      data.rows.sort((a, b) => (a.member_issue > b.member_issue ? 1 : -1));
    if (sort_issue === 'desc')
      data.rows.sort((a, b) => (a.member_issue > b.member_issue ? -1 : 1));

    console.log('sorted: ', sort_name, sort_issue, data.rows);
    console.log('allCopy: ', all.rows);
    console.log('data from sort: ', data.rows);
    this.setState({
      alltable: data,
      alltableCopy: all,
    });
  };

  handleSelectedRadio = (row: number, value: string, table: string) => {
    console.log('handle selected radio: ', row, value);
    let data = {};
    switch (table) {
      case 'partialtable':
        data = this.state.partialtable;
        data['rows'][row]['action'] = value;
        const temp = this.state.partial_items;
        temp[row]['status'] = value;
        this.setFieldValue('partial_items', temp);
        break;
      case 'incompletetable':
        this.setFieldValue('row', row);

        data = this.state.incompletetable;
        if (
          value === 'approve' &&
          data['rows'][row]['incomplete_action'] !== 'approve'
        ) {
          this.openApproveModal();
        } else if (
          value === 'disapprove' &&
          data['rows'][row]['incomplete_action'] !== 'disapprove'
        ) {
          this.openDisapproveModal();
        }

        if (value !== 'approve') {
          delete data['rows'][row]['files'];
        }

        if (value !== 'disapprove') {
          delete data['rows'][row]['remarks'];
        }

        data['rows'][row]['incomplete_action'] = value;
        break;
      case 'conflicttable':
        data = this.state.conflicttable;
        data['rows'][row]['conflict_data_action'] = value;
        break;
      case 'validationtable':
        data = this.state.validationtable;
        data['rows'][row]['validation_action'] = value;
        break;
      case 'disapprovetable':
        data = this.state.disapprovetable;
        data['rows'][row]['disapprove_action'] = value;
        break;
    }

    console.log('selected row: ', data);
    this.setFieldValue(table, data);
  };

  handleBack = () => {
    this.setFieldValue('generateFlag', false);
  };

  handleSaveTmp = () => {
    const payload = {
      user_id: 'verify-member',
      ticket_no: this.props.match.params.ticket_id,
      module: 'Membership',
      process: 'Verify Member',
      timestamp: new Date(),
      data: {
        partial_table: this.state.partialtable,
        incomplete_table: this.state.incompletetable,
        conflict_table: this.state.conflicttable,
        validation_table: this.state.validationtable,
        disapprove_table: this.state.disapprovetable,
        approved_table: this.state.approvedtable,
        all_table: this.state.alltable,
      },
    };

    this.setState({
      loading_state: true,
    });

    API.verifyMemberSaveToTmp(payload)
      .then(() => {
        this.setState({ loading_state: false });
      })
      .catch(() => this.setState({ loading_state: false }));
  };

  handleLoadTmp = () => {
    API.loadVerifyDataFromTmp(this.props.match.params.ticket_id).then(res => {
      if (res[0]) {
        let tmpData = res[0];
        this.setState({ tmpDataModal: true, tmpData: tmpData.data });

        localStorage.setItem('tmp_id', tmpData._id);
        console.log(tmpData._id);
      }
    });
  };

  setTmpValue = () => {
    let tmpData = this.state.tmpData;

    this.setState({
      partialtable: tmpData.partial_table,
      incompletetable: tmpData.incomplete_table,
      conflicttable: tmpData.conflict_table,
      validationtable: tmpData.validation_table,
      disapprovetable: tmpData.disapprove_table,
      approvedtable: tmpData.approved_table,
      alltable: tmpData.all_table,
      tmpDataModal: false,
    });
  };

