  static searchMembers(
    limit: number,
    offset: number,
    filters: any,
    query: string,
  ) {
    let CLIENT_URL =
      API_URL +
      `members/search?limit=${limit}&offset=${offset}&filters=${JSON.stringify(
        filters,
      )}&query=${query}`;
    if (limit === 0) {
      CLIENT_URL =
        API_URL +
        `members/search?offset=${offset}&filters=${JSON.stringify(
          filters,
        )}&query=${query}`;
    }
    return fetch(CLIENT_URL, {
      method: 'get',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
      });
  }

