const hmofielditemStyles = makeStyles({
    disabledhmoname: {
      fontSize: '0.875rem',
      color: 'rgba(39, 46, 76, 0.4)',
      paddingBottom: 4,
    },
    enabledhmoname: {
        fontSize: '0.875rem',
        color: '#151C1B',
        paddingBottom: 4,
    },
});

interface HMOFieldItemProps {
    name?: string;
    value: string;
    disabled?: boolean;
    handleChange?: any;
    required?: boolean;
}

const HMOFieldItem: React.FC<HMOFieldItemProps> = (
	props: HMOFieldItemProps,
): JSX.Element => {

    const classes = hmofielditemStyles(props);

    let error = props.required && (props.value === null || props.value.trim().length < 1);
    let errorMessage = (props.required && (props.value === null || props.value.trim().length < 1)) ? props.name+' is required' : '';

    return(
        <Grid container alignItems="flex-start" style={{paddingTop: 12, paddingBottom: 12, paddingRight: 24}}>
            <Grid item xs={12}>
                <Typography className={props.disabled ? classes.disabledhmoname : classes.enabledhmoname}>{props.name}</Typography>
            </Grid>
            <Grid item xs={12}>
                <InputTextField
                    id={props.name ? props.name.toLowerCase().replace(' ', '-') : ''}
                    disabled={props.disabled}
                    onChange={props.handleChange}
                    error={error}
                    errorMessage={errorMessage}
                    value={props.value} />
            </Grid>
        </Grid>
    );
}

HMOFieldItem.defaultProps = {
    disabled: false,
    name: '',
    value: '',
    required: false,
    handleChange: function(){}
}

