const ordinal_suffix_of = (i) => {
    var j = i % 10,
        k = i % 100;
    if (j === 1 && k !== 11) {
        return i + "st";
    }
    if (j === 2 && k !== 12) {
        return i + "nd";
    }
    if (j === 3 && k !== 13) {
        return i + "rd";
    }
    return i + "th";
}

export const EditHMOInformation: React.FC<EditHMOInformationProps> = (
	props: EditHMOInformationProps,
): JSX.Element => {
    const classes = hmoinfoStyles(props);
    const {
        data,
        contractName,
        editMemberValues,
        plan_type_options,
        MBL,
        ABL,
        PEC,
    } = props;

    function isValidDate(date: string) {
        const d = new Date(date);
        return d instanceof Date && !isNaN(d.getTime());
    }

    let dependents = editMemberValues['dependents'];
    let dependentForms = Object.keys(dependents).map((key) => {
        let dependent = dependents[key];

        return (
            <ExpansionPanel key={key} style={{boxShadow: 'none'}}>
                <ExpansionPanelSummary
                    className={clsx((dependent['index'] % 2 ? 'em-dependent-odd-summary' : 'em-dependent-even-summary'))}
                    id={"panel"+dependent['index']+"-header"}
                    aria-controls={"panel"+dependent['index']+"-content"}
                    expandIcon={<ExpandMoreIcon />}
                >
                    <Typography className={clsx('em-dependent-header')}>{ordinal_suffix_of((dependent['index']+1))+' Dependent'}</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <Grid container style={{paddingLeft: 16, paddingRight: 16, paddingBottom: 24}}>
                        <Grid item xs={12} md={3}>
                            <HMOFieldItem
                                name="First Name"
                                value={dependent['first_name'] ? dependent['first_name'] : ''}
                                required={true}
                                handleChange={(value) => {
                                    props.handleDependentsChange(key, 'first_name', value);
                                }} />
                            <HMOSelectItem
                                name="Suffix"
                                value={dependent['suffix']}
                                options={suffix_options}
                                handleChange={(item) => {
                                    props.handleDependentsChange(key, 'suffix', item.value);
                                }} />
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <HMOFieldItem
                                name="Middle Name"
                                value={dependent['middle_name'] ? dependent['middle_name'] : ''}
                                required={true}
                                handleChange={(value) => {
                                    props.handleDependentsChange(key, 'middle_name', value);
                                }} />
                            <HMOPickerItem
                                name="Birthday"
                                value={dependent['date_of_birth']}
                                handleChange={(bd) => {
                                    props.handleDependentsChange(key, 'date_of_birth', (bd ? bd.toString() : ""));
                                }} />
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <HMOFieldItem
                                name="Last Name"
                                value={dependent['last_name'] ? dependent['last_name'] : ''}
                                required={true}
                                handleChange={(value) => {
                                    props.handleDependentsChange(key, 'last_name', value);
                                }} />
                            <HMOSelectItem
                                name="Civil Status"
                                value={dependent['civil_status']}
                                options={cs_options}
                                handleChange={(item) => {
                                    props.handleDependentsChange(key, 'civil_status', item.value);
                                }} />
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <HMOFieldItem
                                name="Title"
                                value={dependent['title']}
                                handleChange={(value) => {
                                    props.handleDependentsChange(key, 'title', value);
                                }} />
                            <HMOSelectItem
                                name="Gender"
                                value={dependent['gender']}
                                options={gender_options}
                                handleChange={(item) => {
                                    props.handleDependentsChange(key, 'gender', item.value);
                                }} />
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <HMOSelectItem
                                name="Relationship to Principal"
                                value={dependent['relationship_to_principal']}
                                options={rtp_options}
                                handleChange={(item) => {
                                    props.handleDependentsChange(key, 'relationship_to_principal', item.value);
                                }} />
                        </Grid>
                    </Grid>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        );
    });

    return(
        <Grid container style={{paddingLeft: 32, paddingRight: 80, paddingBottom: 80}}>
            <Grid item xs={12} style={{paddingTop: 16, paddingBottom: 16}}>
                <Typography className={classes.formtitle} color="textPrimary">
                    Edit Member Details
                </Typography>
            </Grid>
            <Grid item xs={12} style={{paddingBottom: 8}}>
                <Typography className={classes.formsubtitle} color="textPrimary">
                    HMO Information
                </Typography>
            </Grid>
            <Grid item xs={12} md={4} style={{paddingTop: 0, paddingBottom: 0}}>
                <HMOFieldItem
                    disabled={false}
                    required={true}
                    name="First Name"
                    value={editMemberValues && editMemberValues['first_name'] ? editMemberValues['first_name'] : ''}
                    handleChange={(value) => {
                        props.handleTextChange('first_name', value);
                    }} />
                <HMOSelectItem
                    name="Suffix"
                    value={editMemberValues && editMemberValues['suffix'] ? editMemberValues['suffix'] : ''}
                    options={suffix_options}
                    handleChange={(item) => {
                        props.handleSelectChange('suffix', item);
                    }} />
                <HMOFieldItem disabled={true} name="Member Account No." value="" />
                <HMOFieldItem
                    disabled={true}
                    name="Effectivity Date"
                    value={
                        data && isValidDate(data.effectivity_date)
                        ? moment(data.effectivity_date).format('MMM DD, YYYY')
                        : ''
                    } />
                <HMOFieldItem disabled={true} name="MBL" value={MBL ? MBL : ''} />
                <HMOSelectItem
                    name="Plan Type"
                    value={editMemberValues['plan_types']}
                    options={plan_type_options}
                    handleChange={(item: ISelectItem) => {
                        props.handleSelectChange('plan_type', item);
                    }} />
                <HMOFieldItem
                    disabled={false}
                    required={false}
                    name="Cost Center"
                    value={editMemberValues && editMemberValues['cost_center'] ? editMemberValues['cost_center'] : ''}
                    handleChange={(value) => {
                        props.handleTextChange('cost_center', value);
                    }} />
                <HMOFieldItem
                    disabled={false}
                    required={false}
                    name="Designation"
                    value={editMemberValues && editMemberValues['designation'] ? editMemberValues['designation'] : ''}
                    handleChange={(value) => {
                        props.handleTextChange('designation', value);
                    }} />
                <HMOCheckboxItem
                    name="VIP Member"
                    checked={editMemberValues['is_vip']}
                    handleChange={() => {
                        props.handleCheckboxChange('is_vip');
                    }} />
            </Grid>
            <Grid item xs={12} md={4} style={{paddingTop: 0, paddingBottom: 0}}>
                <HMOFieldItem
                    disabled={false}
                    required={false}
                    name="Middle Name"
                    value={editMemberValues && editMemberValues['middle_name'] ? editMemberValues['middle_name'] : ''}
                    handleChange={(value) => {
                        props.handleTextChange('middle_name', value);
                    }} />
                <HMOSelectItem
                    name="Civil Status"
                    value={editMemberValues && editMemberValues['civil_status'] ? editMemberValues['civil_status'] : ''}
                    options={cs_options}
                    handleChange={(item) => {
                        props.handleSelectChange('civil_status', item);
                    }} />
                <HMOFieldItem disabled={true} name="Member ID" value={data ? data.member_id : ''} />
                <HMOFieldItem
                    disabled={true}
                    name="Contract Renewal Date"
                    value="" />
                <HMOFieldItem disabled={true} name="ABL" value={ABL ? ABL : ''} />
                <HMOFieldItem disabled={true} name="Member Type" value={data ? data.member_type : ''} />
                <HMOFieldItem
                    disabled={false}
                    required={false}
                    name="Financial Code"
                    value={editMemberValues && editMemberValues['financial_code'] ? editMemberValues['financial_code'] : ''}
                    handleChange={(value) => {
                        props.handleTextChange('financial_code', value);
                    }} />
                <HMOFieldItem
                    disabled={false}
                    required={false}
                    name="Job Grade/Level"
                    value={editMemberValues && editMemberValues['job_lvl'] ? editMemberValues['job_lvl'] : ''}
                    handleChange={(value) => {
                        props.handleTextChange('job_lvl', value);
                    }} />
                <HMOCheckboxItem
                    name="Philhealth Rider"
                    checked={editMemberValues['is_philhealth_rider']}
                    handleChange={() => {
                        props.handleCheckboxChange('is_philhealth_rider');
                    }} />
            </Grid>
            <Grid item xs={12} md={4} style={{paddingTop: 0, paddingBottom: 0}}>
                <HMOFieldItem
                    disabled={false}
                    required={true}
                    name="Last Name"
                    value={editMemberValues && editMemberValues['last_name'] ? editMemberValues['last_name'] : ''}
                    handleChange={(value) => {
                        props.handleTextChange('last_name', value);
                    }} />
                <HMOSelectItem
                    name="Gender"
                    value={editMemberValues && editMemberValues['gender'] ? editMemberValues['gender'] : ''}
                    options={gender_options}
                    handleChange={(item) => {
                        props.handleSelectChange('gender', item);
                    }} />
                <HMOFieldItem disabled={true} name="Contract" value={contractName} />
                <HMOFieldItem
                    disabled={true}
                    name="Contract Expiration Date"
                    value={
                        data && isValidDate(data.termination_date)
                        ? moment(data.termination_date).format('MMM DD, YYYY')
                        : ''
                    } />
                <HMOFieldItem disabled={true} name="PEC" value={PEC ? PEC : ''} />
                <HMOFieldItem disabled={true} name="Batch Process No." value="" />
                <HMOFieldItem
                    disabled={false}
                    required={false}
                    name="Site Assigned"
                    value={editMemberValues && editMemberValues['site'] ? editMemberValues['site'] : ''}
                    handleChange={(value) => {
                        props.handleTextChange('site', value);
                    }} />
                <HMOFieldItem
                    disabled={false}
                    required={false}
                    name="Supervisor"
                    value={editMemberValues && editMemberValues['supervisor'] ? editMemberValues['supervisor'] : ''}
                    handleChange={(value) => {
                        props.handleTextChange('supervisor', value);
                    }} />
            </Grid>
            <Grid item xs={12} style={{paddingTop: 24, paddingBottom: 16  }}>
                <Typography className={classes.formsubtitle} color="textPrimary">
                    Dependent Information (Max of 4)
                </Typography>
            </Grid>
            <Grid item xs={12} md={12}>
                {
                    Object.keys(dependents).length > 0 ?
                    dependentForms :
                    (
                        <Typography className={classes.formdesc} style={{paddingTop: 8, paddingBottom: 8}} color="textPrimary">
                            No enrolled dependents.
                        </Typography>
                    )
                }
            </Grid>
        </Grid>
    )
}

EditHMOInformation.defaultProps = {
    data: {},
    contractName: "",
    MBL: '',
	ABL: '',
	PEC: '',
}