interface MemberPageActionBtnsProps {
  onUploadBatchClick: any;
  onGenerateReportClick: any;
}

const MemberPageActionBtns: React.FC<MemberPageActionBtnsProps> = (
  props: MemberPageActionBtnsProps,
): JSX.Element => {
  const classes = memberPageActionBtnsStyles(props);

  const anchorRef = useRef(null);
  const [arrowRef, setArrowRef] = useState<any>(null);
  const [isPopoverOpen, setPopupoverOpen] = useState(false);

  function openPopover() {
    setPopupoverOpen(true);
  }

  function closePopover() {
    setPopupoverOpen(false);
  }

  return (
    <Grid container direction="row" justify="flex-end" spacing={2}>
      <Grid item>
        <FontAwesomeIcon className={classes.icons} icon={faDownload} />
        <span
          className={classes.change_status_link}
          onClick={() => {
            if (props.onGenerateReportClick) {
              props.onGenerateReportClick();
            }
          }}
        >
          Generate Report
        </span>
      </Grid>
      <Grid item>
        <span
          ref={anchorRef}
          className={classes.change_status_link}
          onClick={openPopover}
        >
          <FontAwesomeIcon icon={faPlus} className={classes.icons} />
          Add Member
          <Popper
            open={isPopoverOpen}
            anchorEl={anchorRef.current}
            placement={'bottom-end'}
            className={classes.popper}
            disablePortal={false}
            style={{ marginTop: '4px' }}
            modifiers={{
              flip: {
                enabled: false,
              },
              preventOverflow: {
                enabled: true,
                boundariesElement: 'undefined',
              },
              arrow: {
                enabled: true,
                element: arrowRef,
              },
            }}
          >
            <span className={classes.arrow} ref={setArrowRef} />
            <Paper className={classes.paper}>
              <ClickAwayListener onClickAway={closePopover}>
                <Grid container>
                  <Grid item xs={12}>
                    <Typography
                      className={classes.status}
                      onClick={() => {
                        setPopupoverOpen(false);
                        props.onUploadBatchClick();
                      }}
                    >
                      Upload batch
                    </Typography>
                  </Grid>
                </Grid>
              </ClickAwayListener>
            </Paper>
          </Popper>
        </span>
      </Grid>
    </Grid>
  );
};

type ISelectItem = Components.UI.InputSelect.ISelectItem;
