import * as React from 'react';
import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Dialog, DialogTitle, DialogContent,
        DialogActions, Grid, Button,
        IconButton, Typography
} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';

import { Components } from '@hims/core';
const InputChipTextField = Components.UI.InputChipTextField.InputChipTextField;

interface EmailReportModalProps {
    isModalOpen: boolean;
    id: string;
    email: string;
    onClose: () => void;
    onSend: (data: any) => void;
}

const useStyles = makeStyles(theme => ({
    root: {
        flexWrap:'wrap'
    },
    dialog:{
        align:'center',
        justify:'center',
        width:'95%',
        fontFamily: 'usual',
    },
    dialogContent :{
        paddingTop : '0px',
    },
    dialogContentText :{
        fontSize: '0.875rem'
    },
    container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        minWidth: '680px',
        width: 'auto'
    },
    dialogTitle: {
        paddingTop: theme.spacing(5),
        minWidth: 500
    },
    dialogFirstTitle:{
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        marginBottom: '5px'
    },
    dialogSecondTitle:{
        fontSize: '14px',
        color: '#272E4C'
    },
    dialogAction: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2)
    },
    boldText:{
        fontWeight: 'bold'
    },
    cancelButton: {
        minWidth:129,
        marginRight:25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: "#FFFFFF"
    },
    submitButton: {
        minWidth:129
    },
    previousButton: {
        minWidth:120,
        fontWeight: 600
    },
    nextButton: {
        minWidth:130,
        fontWeight: 600
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    tab:{
        textAlign: 'left',
        alignItems: 'left'
    },
    error:{
        marginTop: 4,
        fontSize: '10px',
        color: '#FF0000'
    }
}));
  

export const EmailReportModal: React.FC<EmailReportModalProps> = (
	props: EmailReportModalProps,
): JSX.Element => {
    const {
        isModalOpen,
        email,
        id,
    } = props;

    const [emailAddress, setEmailAddress] = useState<string[]>([email]);
    const [error, setError] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");
    const classes = useStyles();

    console.log('email: ',email);

    useEffect(() => {
        setEmailAddress([email]);
        console.log('email: ',emailAddress);
    }, [email]);

    function handleEmailReport(){
        emailAddress.forEach(email => {
            console.log('isValid: ',isValidEmailAddress(email));
            if(!isValidEmailAddress(email)) {
                setError(true);
                setErrorMessage('Invalid email address.');
            } else {
                setError(false);
                setErrorMessage('');
                props.onSend(emailAddress);
            }
        });

        if(emailAddress.length === 0){
            props.onSend(emailAddress);
        }
    }

    function isValidEmailAddress(address) {
        return !! address.match(/.+@.+/);
    }

    return (
        <Dialog
            id={id}
            maxWidth={false}
            open={isModalOpen}
            scroll={"paper"}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{zIndex: 9999}}
        >
            <DialogTitle className={classes.dialogTitle} disableTypography={true} id="modal-title" >
                <div className={classes.dialogFirstTitle}>
                    Email Report
                </div>
                <div className={classes.dialogSecondTitle}>
                    
                </div>
                <IconButton aria-label="close" className={classes.closeButton} onClick={props.onClose}>
                    <CloseIcon />
                </IconButton>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Grid item xs={12}>
                    <InputChipTextField
                        label="Email Address"
                        value={emailAddress}
                        error={error}
                        onChange={(items: string[]) => {
                            setEmailAddress(items);
                            console.log('email address: ', items);
                            setError(false);
                            setErrorMessage('');
                        }}
                    />
                    { error ? (
                        <Typography className={classes.error} color="textPrimary">
                            {errorMessage}
                        </Typography>
                        ) : null
                    }
                </Grid>
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                <Grid 
                    container
                    direction="row"
                    justify="space-evenly"
                    alignItems="stretch"
                >
                    <Grid item xs={4} style={{textAlign:'center'}}>
                        <Button
                            className={classes.cancelButton}
                            variant="contained"
                            color="primary"
                            onClick={props.onClose}>
                            Cancel
                        </Button>
                    </Grid>
                    <Grid item xs={4} style={{textAlign:'center'}}>
                        <Button
                            className={classes.submitButton}
                            variant="contained"
                            color="primary"
                            onClick={handleEmailReport}>
                            Send
                        </Button>
                    </Grid>
                </Grid>
            </DialogActions>   
        </Dialog>
    )
}

EmailReportModal.defaultProps = {
    isModalOpen: false,
    id: 'modal-id',
};
